

#BillingContainer{
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    width: 100%;
    padding: 100px;
    background-color:#eeeded;
}
.BillingDetails ,.Payment{
    height: 600px;
    background-color: white;
}

.BillingDetails{
    border-radius: 20px 0px 0px 20px;
    width: 60%;
}
.BillingDetails h3{
    margin: 20px;
    text-align: center;
    font-size: 25px;
}
.Main-Bill-Details{
    margin: 20px;
}
.UserInput{
    display: flex;
    justify-content: space-around;
}
.bllingInput{ 
    width: 350px;
}

.Payment{
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 0px 20px 20px 0px;
    width:30%;
}
.PaymentBox{
    background-color: rgb(246, 246, 246);
    margin-top: 20px;
    height: 360px;
    padding: 20px;
    width: 350px;
    border: 1px solid rgb(174, 173, 173);
    border-radius: 20px;
}
.PaymentBox h3{
    margin: 10px;
    text-align: center;
}

.ServicePrice{
    border-top: 1px  solid  rgb(174, 173, 173);
    border-bottom: 1px  solid  rgb(174, 173, 173);
    display: flex;
    padding: 20px 0px;
    justify-content: space-between;

}

.ServicesPayment{
    display: flex;
    justify-content: space-between;
    margin: 20px 0px;
}
.ServicesPayment p{
    font-size: 15px;
}
.TotalBillPay p{
    font-weight: 700;
}
.PaymetnButton{
    padding: 10px 140px;
    margin-top: 50px;
    color: white;
    font-weight: 700;
    font-size: 18px;
    border-radius: 10px;
    border: none;
    background-color: #35ac75;
}
 @media (max-width:600px) {
    #BillingContainer{
        display: block;
        padding: 10px;
    }
    .BillingDetails{
        border-radius: 20px 20px 0px 0px;
        width: 100%;
        height: 700px;
    }
    .UserInput{
        display: block;
    }
    .bllingInput{ 
        width: 300px;
    }

    .Payment{
        height: 500px;
        width: 100%;
        border-radius: 0px 00px 20px 20px;

    }
    .PaymentBox{
        width: 320px;
    }
    .PaymetnButton{
        padding: 10px 120px;
    }
    .BillingDetails h3{
        margin: 0px;
        padding-top: 20px;
    }
    
    
 }