#Main-Booking {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    width: 100%;
    padding: 100px;
    background-color: #eeeded;

}

.Booking-left,
.Booking-Info {
    background-color: white;
    height: 600px;

}

.Booking-left {
    display: flex;
    justify-content: center;
    /* align-items: center; */
    border-radius: 15px 0px 0px 15px;
    width: 50%;

}

.Booking-Img {
    margin: 50px;
    height: 450px;
    width: 550px;
    border-radius: 20px;
}

.Booking-Img img {
    width: 100%;
    height: 100%;
    border-radius: 20px;
}

.Booking-Button {
    display: flex;
    /* justify-content: center; */

}

.Book-now {
    cursor: pointer;
    border: 1px solid white;
    background-color: orange;
    margin: 50px 20px 0;
    height: 55px;
    width: 400px;
    font-size: 18px;
    border-radius: 10px;
    color: white;
    font-weight: 600;
}
.Book-now:hover{
    background-color: #fb661bdb;

}

.Booking-Info {
    display: flex;
    flex-direction: column;
    /* justify-content: center; */
    border-radius: 0px 15px 15px 0px;
    padding: 20px 0px;
    width: 50%;

}

.Booking-heading {
    margin: 20px;
}

.Booking-Rating {
    display: flex;
    align-items: center;
    margin: 20px;
}

.Booking-Rating span {
    color: orange;
}

.booking-review {
    color: #7e7e7e;
    font-size: 20px;
}

.booking-price {
    margin: 20px;
}

.booking-price span {
    margin: 3px;
}

.booking-main-price {
    font-size: 35px;
    font-weight: 500;

}

.booking-discount-price {

    font-weight: 600;
}

.booking-discount {
    color: red;
    font-weight: 500;
}


.Booking-Description p {
    margin: 20px;
    color: #7e7e7e;
    font-size: 14px;
}
@media (max-width: 600px) {

    #Main-Booking{
        display: block;
        /* justify-content: center;
        align-items: center; */
        padding: 10px;
    }
    .Booking-left{
        width: 100%;
        height: 230px;
        border-radius: 20px 20px 0 0 ;
    
    }
    .Booking-Img {
        margin: 50px;
        height: 450px;
        width: 550px;
        border-radius: 20px;
    }
    .Booking-Img {
        margin: 30px 10px;
        border-radius: 20px;
        border: 1px solid rgba(0, 0, 0, 0.316);
        padding: 0.5px;
        height: 220px;
        width: 310px;
    }
    .Booking-Info{
        display: block;
        width: 100%;
        height: 90%;
        border-radius: 0 ;
    }
    .Booking-heading h1 {
        font-size: 8vw;
    }
    .Booking-Rating {
        align-items: center;
        margin: 0px 20px;
    }
    .booking-price {
        margin: 10px 20px;
    }
    .Book-now{
        margin: 20px;
    }
    .booking-review {
        font-size: 15px;
    }

}
