/* Cropper Modal Container */
.CropperContainer {
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    background: white;
    padding: 20px;
    box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.2);
    border-radius: 10px;
    z-index: 9999;
    width: 80%;
    max-width: 400px;
    text-align: center;
  }
  
  /* Buttons inside Cropper Modal */
  .CropperContainer .SaveChanges,
  .CropperContainer {
    margin: 10px;
    padding: 8px 15px;
    border: none;
    border-radius: 5px;
    cursor: pointer;
  }
  
  /* Customize them as you like */
  .CropperContainer .SaveChanges {
    background-color: orange;
    color: white;
  }
  
  .CropperContainer  {
    background-color: gray;
    color: white;
  }
/* Override for Cropper Modal: allow text selection and pointer events */
.CropperContainer,
.CropperContainer * {
  user-select: text !important;             /* Allow text selection inside modal */
  -webkit-user-select: text !important;
  -moz-user-select: text !important;
  -ms-user-select: text !important;
  
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0.2) !important; /* ya transparent, as per your need */
  pointer-events: auto !important;           /* Ensure buttons and controls receive pointer events */
}
  
#ProfieMainContainer {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    width: 100%;
    padding: 100px;
    background-color: #eeeded;
}


.UserProfile,
.ProfileUserInfo {
    background-color: white;
    height: 600px;
    border-radius: 20px;
    margin: 10px;

}

.UserProfile {
    display: flex;
    justify-content: center;
    padding: 20px;
    width: 20%;
}


.UserImage {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 150px;
    width: 150px;
    margin-left: 35px;
    border-radius: 50%;
}

.UserImage img {
    height: 100%;
    width: 100%;
    border-radius: 50%;
}

.UserProfileEdite {
    display: flex;
    justify-content: center;
    align-items: center;
    position: absolute;
    cursor: pointer;
    top: 235px;
    margin-left: 140px;
    height: 25px;
    width: 25px;
    border-radius: 50%;
    background-color: orange;
}

.UserProfileEdite p {
    color: white;
    font-weight: 500;
}

.UserName {
    text-align: center;
    margin: 10px;
}

.UserProfileSection span {
    font-weight: 600;
    padding: 5px;
}

.PersonalInfo,
.LogOut {
    cursor: pointer;
    height: 35px;
    width: 230px;
    border: 1px solid orange;
    background-color: white;
    color: orange;
    border-radius: 30px;
    margin: 20px 0px;
    padding: 5px 10px;
    /* width: 200px; */
}

.PersonalInformation:hover {
    background-color: orange;
    color: white;
}





.ProfileUserInfo {
    width: 60%;
}

.ProfileUserInfo h3 {
    margin: 20px;
}


.Pesonal-Info {
    display: flex;
}

.Pesonal-Info input {
    margin: 5px 0px;
    background-color: rgb(241, 240, 240);
    border-radius: 10px;
    padding: 6px 15px;
    border: none;
    width: 350px;
}

.ProfileUserInfo label {
    font-size: 15px;
    color: #838282;
}

.Fname,
.Lname {
    margin: 20px;
}

.UserEmail {
    margin: 0 20px;
}

.UserEmail input {
    margin: 5px 0px;
    background-color: rgb(241, 240, 240);
    border-radius: 10px;
    padding: 6px 15px;
    border: none;
    width: 740px;

}

.UserNumber {
    margin: 20px;
}

.UserNumber input {
    margin: 5px 0px;
    background-color: rgb(241, 240, 240);
    border-radius: 10px;
    padding: 6px 15px;
    border: none;
    width: 350px;

}

.GenderInput {
    display: flex;
    align-items: center;
}

.Gender {
    margin: 20px;
}

.Gender label {
    margin-left: 30px;
}

.Gender input {
    margin-left: 5px;
    transform: scale(1.4);
    width: 20px;
}

.InfoSave {
    display: flex;
    justify-content: space-around;

}

.InfoSave button {
    width: 350px;
    border: 1px solid orange;
    border-radius: 30px;
    padding: 10px;
    margin: 40px 0;
    background-color: white;
    color: orange;
    font-size: 15px;
    font-weight: 600;
}

.InfoSave button:hover {
    background-color: orange;
    color: white;
}

@media (max-width:600px) {
    * {
        user-select: none;
    }
    
    #ProfieMainContainer {
        padding-top: 80px;
        display: flex;
        height: 970px;
        flex-direction: column;
    }

    .UserProfileEdite {
        top: 270px;
    }

    .UserProfile {
        width: 330px;
        border-radius: 20px 20px 0px 0px;
        padding-bottom: 0;
    }

    .ProfileUserInfo {
        width: 330px;
        height: 650px;
        border-radius: 0px 0px 20px 20px;
    }

    .ProfileUserInfo h3 {
        text-align: center;
    }

    .UserProfile,
    .ProfileUserInfo {
        margin: 0px;
    }

    .UserProfileSection {
        text-align: center;
    }

    .Pesonal-Info {
        display: block;
    }

    .Pesonal-Info input,
    .UserEmail input,
    .UserNumber input {

        width: 300px;
    }

    .GenderInput {
        display: block;
    }


    .InfoSave button {
        width: 250px;
    }

    .Gender {
        text-align: center;
    }

    .Gender label {
        margin-right: 10px;
    }

    .Gender input {
        margin-right: 30px;
        text-align: center;
    }

  

    .PersonalInfo {
        display: none;
    }
}