/* service content style */
.service-div {
    position: relative;
    top: 85px;
    margin: 20px;
    max-width: 100%;
    height: 450px;
    background-color: white;
}

.sub-menu-top h3 {
    padding: 2vh 2vw;
    font-size: 20px;
    font-weight: 600;
}
.main-ser-container{
    display: flex;
    justify-content: center;
}

.sub-menu {
    display: flex;
    background-color: white;
    align-items: center;
    overflow-x: auto;
}

.sub-menu::-webkit-scrollbar {
    width: 0;
}

.box {
    border-radius: 15px;
    cursor: pointer;
    margin: 10px;
    height: 350px;
    width: 350px;
    border: 1px solid #dadada;
}
.service-info p {
    color: #000000;
    font-size: 17px;
    font-weight: 700;
    text-align: center;
    margin: 10px;
}

.service-info p:nth-of-type(2) {
    color: green;
}
.Main-Ratings{
    color: orange;
    margin: 20px;
    text-align: center;
}


.item {
    width: 100%;
    height: 180px;
    overflow: hidden;
}

.item img {
    border-radius: 15px 15px 0px 0px;
    height: 100%;
    width: 100%;
}

.sub-menu .box:hover {
    
    box-shadow: 0 3px 6px rgba(0, 0, 0, .16), 0 3px 6px rgba(0, 0, 0, .23);
    transition: all .1s ease-in-out;
    scale: 1.04;
}

/* Mobile style  */

@media (max-width: 600px) {
    .service-div {
        top: 0;
        margin: 8px;
        height: 350px;
    }
    .sub-menu-top{
        display: flex;
        align-items: center;
        justify-content: space-between;
    }
    .sub-menu-top p{
        width: 50px;
        height: 25px;
        display: flex;
        padding: 10px;
        align-items: center;
        justify-content: center;
        background-color: rgb(0, 0, 0);;
        margin-right: 15px;
        border-radius: 20px;
        
    }
    .right-arrow{
        color: white;
        font-size: 30px;
    }
    #sub-menu-right-arrow{
        color: white;
        font-size: 25px;
    }

    .sub-menu-top h3 {
        font-size: 15px;
    }

   
    .sub-menu {
        margin: 0vh 2vw;
    }
    .sub-menu .box:hover {
        box-shadow:none;
        transition: none;
        scale: none;
    }

    .box {
        /* padding: 5px; */
        margin: 0px 4px;
        height: 270px;
        width: 200px;
        border-radius: 20px;
    }
    

    .item {
        height: 150px;
        width: 170px;
    }
    .item img{
        border-radius: 20px 20px 0 0;
        height: 100%;
        width: 100%;
    }

    .service-info p {
        font-size: 11px;
        margin: 5px;
    }

}

@media (min-width: 600px) {

    /* menu right arrow  */
#sub-menu-right-arrow{
    display: none;
}
}