* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

#services-main {
    position: relative;
    margin:20px 20px 0px;
    top: 85px;
    max-width: 100%;
    height: 450px;
    background-color: white;

}

#service-type {
    padding: 20px;
}
#service-type h3 {
    font-size: 20px;
    font-weight: 700;
    
}


.service-container{
    display: flex;
    justify-content: center;
}

.service-main-box {
    display: flex;
    background-color: white;
    align-items: center;
    overflow-x: auto;
}
.service-main-box::-webkit-scrollbar{
    width: 0;
}


.service-box {
    /* padding: 10px; */
    border-radius: 15px;
    cursor: pointer;
    margin: 10px;
    height: 350px;
    width: 350px;
    border: 1px solid #dadada;
}
.service-box:hover{
    box-shadow: 0 3px 6px rgba(0, 0, 0, .16), 0 3px 6px rgba(0, 0, 0, .23);
    transition: all .1s ease-in-out;
    scale: 1.04;

}

.service-img {
    width: 100%;
}

.service-img img {
    border-radius: 15px 15px 0px 0px;
    width: 100%;
}

.serv-info {
    margin: 20px;
    width: 100%;
}


.service-name {
    font-size: 15px;
}
.Ratings{
    margin: 10px 0px;
}
.Ratings span{
    color: orange;
}

.price {
    display: flex;
    align-items: center;
    padding: 7px 0px;
}

.main-price {
    font-size: 18px;
    font-weight: 600;
}


.discount-price{
    margin: 0px 10px;
    font-size: 12px;
}

.discount{
    font-size: 12px;
    color: red;
    font-weight: 600;
}


/* Mobile Style  */

@media (max-width:600px) {
    #services-main {
        top: 0;
        margin: 8px;
        height: 900px;
    }
    #service-type {
        padding: 10px ;
    }
    #service-type h3 {
        font-size: 15px;    
    }
    .service-main-box{
        display: grid;
        grid-template-columns: 1fr 1fr ;
    }

    .service-box {
        display: flex;
        flex-direction: column;
        align-items: center;
        margin:5px;
        height: 270px;
        width: 160px;
        border-radius: 20px;
        background-color: #e9e9e975;
    }

    .service-img {
        height: 150px;
        width: 160px;
    }
    .service-img img{
        border-radius: 20px 20px 0 0 ;
        height: 100%;
        width: 100%;
    }
    .serv-info {
        margin: 5px;
        padding: 5px;
    }
    .service-box:hover{
        box-shadow: none;
        transition: none;
        scale: none;
    
    }

    .service-name {
        /* margin: 5px 0px;  */
        font-size: 12px;

    }
    .Ratings{
        margin: 2px 0;
    }
    .price {
        padding: 5px 0px;
    }

    .main-price {
        font-size: 14px;
    }

    .discount-price {
        font-size: 11px;
        margin: 0px 7px;
    }
    
    .discount{
        font-size: 11px;
    }

}