*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

#SofaAd{
    position: relative;
    top: 90px;
    margin: 20px;
    background-color: white;
    height: 80vh;
    max-width: 100%;
     display: flex;
    justify-content: center;
    align-items: center;
}
#SofaImg{
    margin: 0px 1vw;
    width:100vw;
    height: 75vh;
    
}
#SofaImg img{
    border-radius: 10px;
    height: 100%;
    width: 100%;
}
@media (max-width:600px) {
    #SofaAd{
        top: 0;
        margin: 8px;
        height: 28vh;
    }
    #SofaImg{
        margin: 0px 5px;
        width:100vw;
        height: 26vh;
    }
    #SofaImg img{
        border-radius: 10px;
    }


    
}